.navBar{
    background-color: black;
    height: 6.5vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 1%;
    padding-right: 1%;
    font-family: 'Press Start 2P', cursive;
}

.navIcon{
    height: 2.2rem;
    margin-left: 2px;
    margin-right: 5px;
}
.navTitle{
    text-decoration: none;
    color: white;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
}
.navTitle:hover{
    color: #E8632C;
}

.navList{
    display: flex;
    justify-content: space-between;
    width: 30rem;
}
.navItem{
    text-decoration: none;
    color: white;
    font-size: .7rem;
}
.navItem:hover{
    color: #90BA5A;
}
@media only screen and (max-width: 1000px) {

    /* Navbar */

    .navBar{
        background-color: black;
        height: 10.5vh;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-left: 1%;
        padding-right: 1%;
        font-family: 'Press Start 2P', cursive;
    }
    
    .navIcon{
        height: 2rem;
        margin-left: 1px;
    }
    .navTitle{
        text-decoration: none;
        color: white;
        font-size: 1rem;
        display: block;
        align-items: center;
    }
    .navTitle:hover{
        color: #E8632C;
    }
    
    .navList{
        display: flex;
        justify-content: space-between;
        width: 15rem;
    }
    .navItem{
        text-decoration: none;
        color: white;
        font-size: .6rem;
    }
    .navItem:hover{
        color: #90BA5A;
    }

    /* Body for home */

    .imgOne{
        display: flex;
        /* background-size: 100%; */
        /* background-position: center; */
        /* background-size: 100%; */
        /* background-repeat: repeat; */
        /* position: relative; */
        top: 100rem;
    }

    .startButtonContainer{
        display: flex;
        position: absolute;
        background: none;
        background-color: none;
    }
    
    .inputUsername{
        display: flex;
        flex-direction: row;
        height: 50px;
        width: auto;
        text-align: center;
        outline: none;
        border-radius: 50px;
    }
    
    .homeButton{
        display: flex;
        flex-direction: row;
        cursor: pointer;
        color: white;
        font-size: 20px;
        text-decoration: none;
        padding-top: 50px;
        padding-left: 67px;
    }
    .homeButton:hover{
        color: #89B550;
    }
    .homeBottomContainer{
        background-color: #0E0E0E;
        height: 35%;
        width: 100%;
    }

}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    background-color: black;
}

.homeBody{
    height: 93.5vh;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    font-family: 'Press Start 2P', cursive;
}
.homeBanner{
    height: 100%;
    width: 100vw;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.imgOne{
    width: 100vw;
    position: relative;
    top: 2rem;
}
.homeLogo{
    position: absolute;
    margin-top: 10rem;
}

.startButtonContainer{
    display: block;
    position: absolute;
    background: none;
    background-color: none;
    margin-top: 25rem;
}

.inputUsername{
    display: flex;
    flex-direction: row;
    height: 50px;
    width: auto;
    text-align: center;
    outline: none;
    border-radius: 50px;
}

.homeButton{
    display: flex;
    flex-direction: row;
    cursor: pointer;
    color: white;
    font-size: 20px;
    text-decoration: none;
    padding-top: 50px;
    padding-left: 67px;
}
.homeButton:hover{
    cursor: pointer;
    color: #89B550;
}
.homeBottomContainer{
    background-color: #0E0E0E;
    height: 35%;
    width: 100%;
}

/* MEDIA QUERIES */

@media only screen and (max-width: 1000px) {
    .startButtonContainer{
        display: block;
        position: absolute;
        background: none;
        background-color: none;
        padding-top: 240px;
        padding-right: 25px;
        margin-top: 10rem;
    }
    .inputUsername{
        display: flex;
        flex-direction: row;
        height: 40px;
        width: auto;
        text-align: center;
        outline: none;
        border-radius: 50px;
    }
    
    .homeButton{
        display: flex;
        flex-direction: row;
        cursor: pointer;
        color: white;
        font-size: 20px;
        text-decoration: none;
        padding-top: 30px;
        padding-left: 70px;
    }
}
.gameBody{
    background-color: #0e0e0e;
    height: 100vh;
    display: flex;
    font-family: 'Press Start 2P', cursive;
    background-size: 100%;
    background-position: center;
}


/* -- INFO SIDE -- */
.infoSide{
    height: 100%;
    width: 40vw;
}
.usernameArea{
    display: flex;
    align-items: flex-end;
    height: 10vh;
}
.infoArea{
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* -- GAME SIDE -- */
.gameSide{
    height: 100%;
    width: 60vw;
}
.progressionArea{
    height: 20vh;
    display: flex;
}
.eventArea{
    width: 45%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}
.townArea{
    width: 55%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.dungeonArea{
    height: 55vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.handArea{
    height: 25vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.infoBody{
    background-color: #6C6D73;
    height: 95%;
    width: 95%;
    border-radius: 5px;
    color: white;
    border-style: solid;
    border-width: 5px;
    border-color: #9A7C4A;
}

/* -- INFO AREA -- */
.cardInfoArea{
    height: 45%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.displaySection{
    width: 40%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
}
.cardDisplay{
    background-color: black;
    height: 90%;
    width: 88%;
    border-style: solid;
    border-width: 3px;
    border-color: white;
    border-radius: 0px;
}

.infoSection{
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-right: 10px;
    padding-top: 3%;
}
.title{
    font-size: 20px;
    margin-bottom: 0px;
}
.information{
    font-size: 15px;
    margin-bottom: 5px;
    font-weight: lighter;
}

.cardDescription{
    font-size: 12px;
    margin-bottom: 5px;
    font-weight: lighter;
}

/* -- BUTTON AREA -- */
.buttonArea{
    height: 15%;
}
.phaseInfo{
    height: 45%;
    display: flex;
    align-items: center;
    padding-left: 4%;
    font-size: 20px;
}
.buttonList{
    height: 55%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 4%;
    padding-right: 4%;
    padding-top: 0px;
    padding-bottom: 0px;
}
.button{
    color: #474747;
    font-size: 20px;
    min-width: 7rem;
    border-style: solid;
    border-width: 3px;
    border-color: #313131;
    border-radius: 5px;
    text-align: center;
    background-color: #c1c1c1;
}
.button:hover{
    background-color: #474747;
    border-color: #c1c1c1;
    color: #c1c1c1;
    cursor: pointer;
}
/* class for BUILD button when buildingModeState is true */
.buttonBuild{
    background-color: #474747;
    border-color: #c1c1c1;
    color: #c1c1c1;
    font-size: 20px;
    min-width: 7rem;
    border-style: solid;
    border-width: 3px;
    border-radius: 5px;
    text-align: center;
}
.buttonBuild:hover{
    border-color: #313131;
    cursor: pointer;
}

/* -- MESSAGE AREA -- */
.messageArea{
    height: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.messageBox{
    background-color: #474747;
    width: 92%;
    height: 80%;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 5px;
    border-style: solid;
    border-width: 2px;
    border-color: #313131;
    border-radius: 5px;
}
.message{
    font-size: 14px;
}
/* classes for card count when dungeon rooms are stacked */
.cardCountContainer{
    text-align: center;
    width: 88%;
}
.cardCountLabel{
    text-align: left;
}
.cardStackButtons{
    width: 100%;
    text-align: right;
    font-size: 20px;
}
.cardStackButtons:hover{
    color: #474747;
    cursor: pointer;
}


.handContainer{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    margin-top: -7%;
}
.handBody{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    overflow-x: scroll;
}
.handCard{
    border-style: solid;
    border-width: 3px;
    border-color: white;
    border-radius: 5px;
    height: auto;
    width: 15.3%;
    margin-right: 13px;
}
.handCard:hover{
    border-color: #D93104;
    border-radius: 0px;
    border-width: 2px;
    cursor: pointer;
}
.discardCard{
    border-style: solid;
    border-width: 3px;
    border-color: white;
    border-radius: 5px;
    height: auto;
    width: 15.3%;
    margin-right: 13px;
}
.discardCard:hover{
    border-color: #04d9b5;
    border-radius: 0px;
    border-width: 2px;
    cursor: pointer;
}
.cardDeckLabel{
    color: white;
    font-size: 1.5rem;
}

.eventBody{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
    font-size: 2rem;
    padding-left: 5%;
}

/* -- HEARTS -- */
.health{
    display: flex;}
.heart{
    height: 2.8rem;
}

/* -- SOULS -- */

.souls{
    display: flex;
    font-size: 1.5rem;
}
.soul{
    margin-left: 10px;
    font-size: 1.5rem;
}

/* -- ROUNDS -- */

.rounds{
    display: flex;
    font-size: 1.5rem;
}
.round{
    margin-left: 10px;
    font-size: 1.7rem;
}
.townBody{
    height: 100%;
    width: 100%;
    color: white;
}

.townTitle{
    height: 20%;
    font-size: 20px;
    display: flex;
    align-items: flex-end;
}

.townList{  
    height: 80%;
    display: flex;
    align-items: center;
    overflow-x: scroll
}
.townCard{
    border-style: solid;
    border-width: 3px;
    border-color: black;
    border-radius: 5px;
    height: 100%;
    width: 18%;
    margin-right: 5px;
}
.townCard:hover{
    border-color: #4F93B3;
    border-width: 2px;
    border-radius: 0px;
    cursor: pointer;
}
.usernameBody{
    color: white;
    font-size: 40px;
    padding-left: 3%;
}
.usernameBody:hover{
    color: #90BA34;
}
.dungeonBody{
    height: 100%;
    width: 95%;
}

/* -- HERO AREA -- */
.heroDisplay{
    height: 40%;
    width: 80%;
    display: flex;
    /* display: inline; */
    justify-content: flex-end;
    align-items: center;
}
.hero{
    border-style: solid;
    border-width: 3px;
    border-color: black;
    border-radius: 5px;
    height: 90%;
    width: 16%;
    margin-left: .6%;
}
.hero:hover{
    border-color: #4F93B3;
    border-radius: 0px;
    border-width: 2px;
    cursor: pointer;
}

/* -- DUNGEON AREA -- */
.dungeonDisplay{
    height: 60%;
    display: flex;
}
.roomArea{
    height: 70%;
    width: 80%;
    background-color: #5B5B5B;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-style: solid;
    border-width: 5px;
    border-color: #534125;
}
.roomAreaBuilding{
    height: 70%;
    width: 80%;
    background-color: #cccccc;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-style: solid;
    border-width: 5px;
    border-color: #c78720;
}
.room{
    border-style: solid;
    border-width: 3px;
    border-color: #5B5B5B;
    border-radius: 5px;
    width: 100%;
    height: auto;
}
.roomStack{
    border-style: solid;
    border-width: 3px;
    border-color: #5B5B5B;
    border-radius: 5px;
    width: 100%;
    margin-left: .7%;
}
.room2{
    height: 88%;
    width: 16%;
    margin-left: .7%;
    margin-bottom: 2%;
    text-align: center;
}
.room:hover{
    border-style: solid;
    border-width: 2px;
    border-color: #D93104;
    border-radius: 0px;
    cursor: pointer;
}
.roomStack:hover{
    border-style: solid;
    border-width: 2px;
    border-color: #d9cb04;
    border-radius: 0px;
    cursor: pointer;
}

.bossArea{
    height: 90%;
    width: 20%;
    background-color: #474747;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.boss{
    height: 87%;
    width: 85%;
    border-style: solid;
    border-width: 3px;
    border-color: #474747;
}
.boss:hover{
    border-style: solid;
    border-width: 2px;
    border-color: #BD8810;
    border-radius: 0px;
    cursor: pointer;
}
.durabilityLabel{
    font-size: 1.8vh;
}
